import { Controller, Block } from '@ali/4ever-cangjie';
import { ColorBlocks } from '@ali/4ever-mo';

export default function (controller: Controller, data: { node: Block }) {
  const { node } = data || {};
  const parent = node && controller.value.document.getParent(node.key);
  if (
    ColorBlocks.isColorBlocks(parent) &&
    parent.data.metadata.showstk &&
    node === parent.nodes[0]
  ) {
    return {
      x: -38,
      y: 0,
    };
  }
  if (ColorBlocks.isColorBlocks(parent)) {
    return {
      x: 2,
      y: 0,
    };
  }
  return undefined;
}
